.header {
  background-color: #f2f2f2;
  padding-top: 25px;
  padding-bottom: 15px;
}

.logo {
  // outline: 1px solid red;
}

.logo-text-wrapper {
  font-family: var(--logo-font);
  font-size: 58px;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo-words-wrapper {
  display: flex;
  flex-wrap: wrap;

  word-spacing: -8px;
  letter-spacing: 2px;
}

.logo-word1 {
  display: block;
  line-height: 0.8;
  color: #ffc602;
  font-weight: 700;
}

.logo-word2 {
  display: block;
  line-height: 0.8;
  color: #000;
  font-weight: 700;
}

.logo-quote {
  display: block;
  line-height: 0.8;
  font-size: 23px;
  color: #5a5a5a;
}

//  ------ address ------
.address {
  margin-top: 25px;
}

.address-list {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media screen and (min-width: 406px) {
    flex-direction: row;
    justify-content: center;
  }
}

.address-list-li:not(:last-child) {
  margin-right: 20px;
}

.adress-list-link {
  font-size: 18px;
  color: rgb(0, 0, 0);
  font-weight: 700;
  font-style: normal;
  text-transform: uppercase;
}

.adress-list-svg {
  // background-color: #fff;
  width: 14px;
  height: 14px;

  // outline: 1px solid red;
}

.adress-list-svg:hover,
.adress-list-link:hover {
  // fill: rgb(235, 133, 0);
  // color: rgb(235, 133, 0);
}
// ---------------------------

// ------ NAV ------
.nav {
  // outline: 2px solid green;
  position: relative;
  margin-top: 10px;
}

.hamburger {
  padding: 15px 25px;
  text-align: center;
  text-decoration: none;
  background-color: #222222;
  color: #fff;

  font-weight: 700;
  display: block;
  z-index: 1;

  cursor: pointer;
}

.menu {
  z-index: 2;
  position: absolute;
  width: 100%;
  bottom: -300%;
  // outline: 1px solid red;
  transition: linear 0.5s;

  pointer-events: none;
  opacity: 0;
}

.showMenu {
  bottom: -410%;
  background-color: #222222;

  pointer-events: auto;
  opacity: 1;

  // box-shadow: 1px 0 3px #ffc602;
}

.menu-item {
  display: block;
  padding: 10px 20px;

  color: rgb(0, 0, 0);
  background-color: #d1d1d1;
  font-weight: 700;

  border-bottom: 1px solid rgb(128, 116, 116);
}

